*{
  margin:0;
  padding:0;
}

body{
  background-color: #409895;
}

#root{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App{
  width:100%;
  min-width: 370px;
  max-width: 550px;
  height:83vh;
  background-color:#ffffff;
  display: flex;
  flex-direction: column;
  padding: 30px;
  border-radius: 30px;
}

.Gif{
  height: 134px;
  width: 134px;
  margin: 0 auto;
  margin-bottom: 10px;
}

.Heading{
  text-align: center;
  margin-bottom: 7px;
}

.Content{
  text-align: center;
  width: 80%;
  margin: 0 auto;
}

.Inputs{
  position: relative;
}

.Input{
  width: 75%;
  margin-top: 25px;
  margin-left: 30px;
  padding: 8px;
  border-radius: 10px;
}

.Button{
  background-color: #409895;
  color: black;
  font-weight: bolder;
  font-size: 12px;
  padding: 10px;
  width:60px;
  border-radius: 15px;
  margin-left: 10px;
  cursor: pointer;
  border: none;
  text-align: center;
  user-select: none;
}

.Reload{
  font-size: 28px;
  position: absolute;
  left: 77.5%;
  top:30px;
  cursor: pointer;
  user-select: none;
}

.Type{
  color:red;
  margin-left: 30px;
}

.Length{
  margin:10px 0 30px 30px;
}

.SliderContainer{
  display: flex;
  justify-content: center;
}

.slider {
  /* -webkit-appearance: none; */
  width: 90%;
  height: 15px;
  border-radius: 5px;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  cursor: pointer;
}

.Checkbox-Container{
  width: 100%;
  height: 200px;
  max-width: 490px;
  margin: 20px auto 0;
}

.Wrap{
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}


